<template>
  <div>
    <router-link
      to="/"
      class="header-abs"
      v-show="showAbs"
    >
      <div class="iconfont header-abs-back">&#xe6a4;</div>
    </router-link>
    <div class="header-fixed" v-show="!showAbs" :style="opacityStyle">
      城市详情页
      <router-link to='/'>
        <div class="iconfont header-back-icon">&#xe6a4;</div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  data () {
    return {
      showAbs: true,
      opacityStyle: {
        opacity: 0
      }
    }
  },
  methods: {
    handleScroll () {
      const top = document.documentElement.scrollTop
      if (top > 60) {
        let opacity = top / 140
        opacity = opacity > 1 ? 1 : opacity
        this.opacityStyle = {
          opacity
        }
        this.showAbs = false
      } else {
        this.showAbs = true
      }
    }
  },
  activated () {
    window.addEventListener('scroll', this.handleScroll)
  },
  deactivated () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="stylus" scoped>
  .header-abs
    position: absolute
    left: 10px
    top: 10px
    width: 40px
    height: 40px
    border-radius: 20px
    text-align: center
    background: rgba(0, 0, 0, .8)
    text-decoration: none
    .header-abs-back
      font-size: 20px
      line-height: 40px
      color: #fff
  .header-fixed
    height: 43px
    line-height: 43px
    text-align: center
    color: #fff
    background: #00bcd4
    position: fixed
    top: 0
    left: 0
    right: 0
    .header-back-icon
      width: 50px
      font-size: 25px
      text-align: center
      position: absolute
      left: 0
      top: 0
      color: #fff
</style>



