<template>
  <div>
    <div class="title">周末去哪儿</div>
    <div class="items" v-for="item of list" :key="item.id">
      <div>
        <div class="item-img-wrapper">
          <img class="img-content" :src="item.imgUrl" />
        </div>
        <div class="item-info">
          <div class="info-name">{{item.name}}</div>
          <div class="info-desc">{{item.desc}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeWeekend',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
  .title
    height: 40px
    padding-left: 13px
    line-height: 40px
    background: #eee
    color: #212121
  .item-img-wrapper
    overflow: hidden
    height: 0
    padding-bottom: 37.3%
    .img-content
      width: 100%
  .item-info
    padding: 7px 10px 10px 10px
    .info-name
      overflow: hidden
      padding-right: 70px
      color: #212121
      font-size: 14px
      line-height: 24px
    .info-desc
      overflow: hidden;
      padding-right: 70px;
      color: #616161;
      font-size: 12px;
      line-height: 24px;
</style>



