<template>
  <div class="header">
    <div class="header-left">
      <div class="iconfont back-icon">&#xe6a4;</div>
    </div>
    <div class="header-input">
      <span class="iconfont">&#xe802;</span>
    </div>
    <router-link to='/city'>
      <div class="header-right">
        {{this.city}}
        <span class="iconfont">&#xe6e9;</span>
      </div>
    </router-link>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style lang="stylus" scoped>
  a
    text-decoration: none
    color: #fff
  .header
    display: flex
    height: 43px
    background: #00bcd4
    line-height: 43px
    color: #fff
    .header-left
      width: 32px
      float: left
      .back-icon
        position: absolute
        left: 5px
        font-size: 20px
    .header-input
      flex: 1
      background: #fff
      border-radius: 5px
      height: 32px
      margin-left: 4px
      margin-right: 4px
      margin-top: 6px
      color: #ccc
      line-height: 32px
      padding-left: 2px
    .header-right
      min-width: 52px
      padding: 5px
      float: right
      text-aligin: center
</style>



