<template>
  <div>
    <div class="banner" @click="handleBannerClick">
      <img class="img-content" :src="bannerImg"/>
      <div class="banner-info">
        <div class="banner-title">
          {{this.sightName}}
        </div>
        <div class="banner-number">{{this.imgLen}}<span class="iconfont banner-icon">&#xe633;</span></div>
      </div>
    </div>
    <common-fade>
      <common-gallary :imgs="gallaryImgs" v-show="showGallary" @close="handleGallaryClick"></common-gallary>
    </common-fade>
  </div>
</template>

<script>
import CommonGallary from 'common/gallary/Gallary'
import CommonFade from 'common/fade/Fade'
export default {
  name: 'DetailBanner',
  data () {
    return {
      showGallary: false
    }
  },
  props: {
    sightName: String,
    bannerImg: String,
    gallaryImgs: Array
  },
  components: {
    CommonGallary,
    CommonFade
  },
  computed: {
    imgLen () {
      return this.gallaryImgs.length
    }
  },
  methods: {
    handleBannerClick () {
      this.showGallary = true
    },
    handleGallaryClick () {
      this.showGallary = false
    }
  }
}
</script>

<style lang="stylus" scoped>
  .banner
    position: relative
    overflow: hidden
    height: 0
    width: 100%
    padding-bottom: 55%
    .img-content
      width: 100%
    .banner-info
      display: flex
      position: absolute
      right: 0
      left: 0
      bottom: 0
      line-height: 30px
      color: #fff
      background-image: linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8))
      .banner-title
        flex: 1
        font-size: 16px
        padding: 0 10px
      .banner-number
        padding: 0 20px
        height: 16px
        line-height: 16px
        border-radius: 10px
        background: rgba(0,0,0,.8)
        margin-top: 7px
        font-size: 12px
        .banner-icon
          font-size: 12px
</style>



