<template>
  <div>
    <div class="search">
      <input class="search-input" type="text" placeholder="请输入城市名或拼音"
      v-model="keyword"
      />
    </div>
    <div class="search-content" ref="wrapper" v-show="keyword">
      <ul>
        <li v-for="item of list" :key="item.id"
        class="search-item border-bottom"
        @click="handleCityClick(item.name)"
        >
          {{item.name}}
        </li>
        <li class="search-item border-bottom" v-show="hasNoList">没有找到匹配数据</li>
      </ul>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import { mapMutations } from 'vuex'
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      keyword: '',
      list: [],
      timer: null
    }
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city)
      this.$router.push('/')
    },
    ...mapMutations(['changeCity'])
  },
  computed: {
    hasNoList () {
      return !this.list.length
    }
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      if (!this.keyword) {
        this.list = []
        return
      }
      this.timer = setTimeout(() => {
        const result = []
        for (let i in this.cities) {
          this.cities[i].forEach((value) => {
            if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
              result.push(value)
            }
          })
        }
        this.list = result
      })
    }
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.wrapper)
  }
}
</script>

<style lang="stylus" scoped>
  .search
    height: 36px
    padding: 0 5px 0 5px
    background: #00bcd4
    .search-input
      box-sizing: border-box
      display: block
      height: 33px
      ling-height: 31px
      padding: 0 5px
      border: 0
      width: 100%
      text-align: center
      border-radius: 3px
  .search-content
    z-index: 1
    overflow: hidden
    position: absolute
    top: 79px
    left: 0
    right: 0
    bottom: 0
    background: #ccc
    .search-item
      line-height: 31px
      padding-left: 10px
      color: #666
      background: #fff
</style>



