<template>
  <div class="wrapper">     <!--wrapper 避免加载慢时突然渲染 -->
    <swiper :options="swiperOption">
      <!-- slides -->
      <swiper-slide v-for="item of list" :key="item.id">
        <img class="swiper-img" :src="item.imgUrl" />
      </swiper-slide>
      <!-- Optional controls -->
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeSwiper',
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        loop: true
      }
    }
  },
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
  .swiper-img
    width: 100%
  .wrapper
    overflow: hidden
    width: 100%
    height: 0
    padding-bottom: 26%
</style>



