<template>
  <div>
    <div class="title">热销推荐</div>
    <ul>
      <router-link
        tag="li"
        class="item"
        v-for="item of list"
        :key="item.id"
        :to="'/detail/' + item.id"
       >
        <img class="img-content" :src="item.imgUrl">
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
          <button class="item-btn">查看详情</button>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeRecommend',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
  .title
    margin-top: 10px
    line-height: 40px
    background: #eee
    text-indent: 10px
    font-size: 16px
  .item
    height: 100px
    padding: 10px 0
    display: flex
    overflow: hidden
    .img-content
      padding-left: 10px
      height: 100px
      width: 100px
    .item-info
      padding-left: 11px
      flex: 1
      .item-title
        font-size: 16px
        line-height: 22px
      .item-desc
        font-size: 12px
        color: #ccc
        line-height: 20px
      .item-btn
        background: #ff9300
        padding: 10px
        border-radius: 3px
        margin-top: 15px
        color: #fff
</style>



